export default{
	data() {
		return {
			foodId:'',
			idFoodLists:'',
			IngredientsList:'',
			urlV:""+new Date().getTime()
		}
	},
	onLoad(option){
		let { id } = option 
		this.foodId=id
		this.getFoodInfo(this.foodId)
	},
	methods:{
		getFoodInfo(foodId){
			this.$request({
				url:"/front/food/foodIngredientsList",
				data:{
					foodId:foodId,
				}
			}).then(res => {
				this.idFoodLists=res.data.data.idFoodLists
				this.IngredientsList=res.data.data.IngredientsList
			})
		}
		
	}
}

